  padding-block: 41px 42px;
  color: var(--base-light);
  background-color: var(--base-dark);
}
@media (max-width: 1199px) {
  .page-footer {
    padding-block: 43px;
  }
}
@media (max-width: 767px) {
  .page-footer {
    padding-block: 38px 51px;
  }
}

.page-footer__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1199px) {
  .page-footer__wrapper {
    padding-top: 3px;
  }
}
@media (max-width: 767px) {
  .page-footer__wrapper {
    flex-direction: column;
  }
}

.page-footer__navigation {
  display: contents;
}
@media (max-width: 767px) {
  .page-footer__navigation {
    display: block;
    width: 100%;
    columns: 2;
    column-gap: 30px;
    margin-bottom: 15px;
    padding-top: 7px;
  }
}

.page-footer__logo {
  transform: translateY(6px);
}
@media (max-width: 1199px) {
  .page-footer__logo {
    max-width: 81px;
    transform: translateY(0);
  }
}
@media (max-width: 767px) {
  .page-footer__logo {
    display: inline-grid;
    place-items: start;
    max-width: 68px;
    margin-bottom: 23px;
  }
}

.page-footer__menu {
  max-width: 70%;
  margin: 0;
  padding: 0;
  padding-left: 9px;
}
@media (max-width: 1199px) {
  .page-footer__menu {
    padding-inline: 13px 0;
  }
}
@media (max-width: 767px) {
  .page-footer__menu {
    width: 100%;
    padding: 0;
  }
}

.page-footer__social {
  max-width: 10%;
  margin: 0;
  padding: 0;
}
@media (max-width: 1199px) {
  .page-footer__social {
    max-width: 15%;
    translate: 6px 0;
  }
}
@media (max-width: 767px) {
  .page-footer__social {
    align-self: start;
    max-width: 100%;
    translate: 0;
  }
}

.main-menu {
  display: flex;
  flex-wrap: wrap;
  column-gap: 40px;
  row-gap: 20px;
}
@media (max-width: 1199px) {
  .main-menu {
    column-gap: 30px;
  }
}
@media (max-width: 767px) {
  .main-menu {
    display: contents;
  }
}

.main-menu__item::marker {
  color: transparent;
}
@media (max-width: 767px) {
  .main-menu__item {
    display: grid;
    place-items: start;
    margin-bottom: 16px;
  }
}

.main-menu__link {
  --scale-top: scaleX(0);
  --scale-bottom: scaleX(0);
  --color: inherit;
  position: relative;
  font: 400 14px/16px "Roboto Condensed", "Arial", sans-serif;
  color: var(--color);
  user-select: none;
}
.main-menu__link::before {
  content: "";
  position: absolute;
  top: -3px;
  z-index: 1;
  display: block;
  width: 100%;
  height: 0.06em;
  background-color: currentColor;
  transform: var(--scale-top);
  transform-origin: 50%;
  transition: 300ms transform ease-out;
}
.main-menu__link::after {
  content: "";
  position: absolute;
  bottom: -3px;
  z-index: 1;
  display: block;
  width: 100%;
  height: 0.06em;
  background-color: currentColor;
  transform: var(--scale-bottom);
  transform-origin: 50%;
  transition: 300ms transform ease-out;
}
@media (hover: hover) {
  .main-menu__link:hover:not(.focus-visible) {
    --scale-bottom: scaleX(1);
  }
}
.main-menu__link.focus-visible:focus {
  --scale-top: scaleX(1);
  --scale-bottom: scaleX(1);
  outline: none;
}
@media screen {
  .main-menu__link:active:not(.focus-visible) {
    opacity: 0.8;
  }
  .main-menu__link.focus-visible:active {
    opacity: 0.8;
  }
}
@media (max-width: 767px) {
  .main-menu__link {
    padding-bottom: 5px;
    font: 400 16px/19px "Roboto Condensed", "Arial", sans-serif;
  }
}

.social {
  display: flex;
  flex-wrap: wrap;
  align-content: start;
  gap: 11px;
}
@media (max-width: 1199px) {
  .social {
    gap: 9px;
  }
}

.social__item::marker {
  color: transparent;
}

.social__link {
  --icon-size: 25px;
  --color: var(--base-light);
  display: grid;
  place-items: center;
  min-width: var(--icon-size);
  width: var(--icon-size);
  height: var(--icon-size);
  color: var(--color);
}
@media (max-width: 1199px) {
  .social__link {
    --icon-size: 26px;
  }
}
@media (hover: hover) {
  .social__link:hover:not(.focus-visible) {
    --color: var(--themed-quiet);
  }
}
.social__link.focus-visible:focus {
  --color: var(--themed-quiet);
}
@media screen {
  .social__link:active:not(.focus-visible) {
    opacity: 0.8;
  }
  .social__link.focus-visible:active {
    opacity: 0.8;
  }
}

.social__img {
  width: 100%;
  height: auto;
}